body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


span {
  display: block;
}
.filter-container {
    width: 500px;
    padding: 6px;
}

.quicklinks {
    background-color: #1b756d;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.quicklinks-container {
    width: 95%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.quicklinks-lhs {
    display: flex;
    width: 50%;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}

.quicklinks-rhs {
    display: flex;
    width: 50%;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-end;
}

.quicklinks-lhs > span, .quicklinks-rhs > span {
    padding: 12px;
}

.quicklinks-lhs > span {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.quicklinks-rhs > span {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.quicklinks-lhs > span > a, .quicklinks-rhs > span > a {
    color: ivory;
    font-size: 10.5px;
    transition: all ease-in-out 300ms;
    -webkit-transition: all ease-in-out 300ms;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    -webkit-text-decoration-color: transparent;
            text-decoration-color: transparent;
}

.quicklinks-lhs > span > a:hover, .quicklinks-rhs > span > a:hover {
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    -webkit-text-decoration-color: aliceblue;
            text-decoration-color: aliceblue;
}

.header-container {
    background-color: #1e8178;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mat-toolbar {
    width: 95%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    background-color: #1e8178;
}

.left-hand-side {
    width: 65%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.right-hand-side {
    width: 35%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.left-hand-side > span, .right-hand-side > span {
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    color: ivory;
}

.left-hand-side > span > button, .right-hand-side > span > button {
    font-size: 12px;
}

.right-hand-side > span > span {
    font-size: 13px;
}

.searchBox {
    width: 570px;
    height: 30px;
    border: 2px solid ivory;
    border-radius: 5px;
    padding: 6px;
}

.searchBox::-webkit-input-placeholder {
    font-size: 11px;
}
.nav-group {
    width: 100%;
    background-color: #1e8178;
}

.nav {
    color: ivory;
    font-size: 16px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.nav-left {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.nav-right {
    display: flex;
    width: 0%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.lenskart-searchbox {
    padding: 10px;
    width: 300px;
    background-color: lightgrey;
    border: none;
}

.nav-tablets-group {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
}

.nav-tablet {
    padding: 10px 10px 10px 10px;
    width: 20%;
    text-align: center;
}

.nav-tablet:first-child>a {
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
}

.nav-tablet>a {
    padding-top: 10px;
    padding-bottom: 10px;
    color: ivory;
    width: 100%;
}

.nav-tablet:hover {
    background-color: honeydew;
}

.nav-tablet:hover a {
    color: #1e8178;
}

.nav-tablet:hover .nav-tab-submenu-container {
    position: absolute;
    display: block;
    margin-top: 10px;
    width: 100%;
}

.nav-tab-submenu-container {
    position: relative;
    display: none;
    left: 0px;
    right: 0px;
    padding: 0;
    margin: 0;
	width: 0%;
    background-color: honeydew;
    border: none;
    z-index: 100;
}

.nav-image-tablet-group {
    padding: 0;
}

.nav-image-tablet-group>li {
    display: block;
    float: left;
    margin-left: 8px;
    margin-right: 8px;
    padding-top: 8px;
    padding-bottom: 8px;
    margin: 0;
    width: 20%;
    background-color: honeydew;
}

.nav-image-tablet-group>li:hover {
    background-color: ivory;
    height: 100%;
}

.nav-image-tablet-group>li:hover .nav-submenu-container {
    position: absolute;
    display: block;
    margin: 0px;
    height: 450px;
    width: 100%;
}

/*.nav-tablet:hover .nav-submenu-container {
    position: absolute;
    display: block;
    margin-top: 0px;
    height: 400px;
    width: 99.7%;
}*/

.nav-submenu-container {
	position: relative;
    display: none;
    left: 0px;
    right: 0px;
    padding: 0;
    margin: 0;
    height: 0px;
	width: 0%;
    background-color: ivory;
    border: none;
    z-index: 100;
}

.nav-submenu-text-aid {
    position: relative;
    display: block;
    width: 45%;
    padding-top: 10px;
    float: left;
    padding: 10px;
    text-align: left;
}

.nav-submenu-visual-aid {
    position: relative;
    display: block;
    width: 45%;
    padding-top: 10px;
    float: left;
    min-height: 150px;
    padding: 10px;
    text-align: left;
}

.nav-submenu-text-aid>ul {
    position: relative;
    display: block;
    padding-top: 10px;
    float: left;
    min-height: 150px;
    padding: 10px;
}

.nav-submenu-visual-aid>ul {
    position: relative;
    display: block;
    padding-top: 10px;
    float: left;
    min-height: 150px;
    padding: 10px;
}

.nav-submenu-text-aid>ul>li:hover>a, .nav-submenu-visual-aid>ul>li:hover>a {
    color: black;
}

li {
    list-style: none;
}

.nav-submenu-visual-aid>span>a>img {
    padding: 10px;
}

a, .nav-submenu-text-aid>ul>li:first-child>a, .nav-submenu-visual-aid>ul>li:first-child>a {
    text-decoration: none;
    text-transform: uppercase;
    color: #797979;
    cursor: pointer;
}

.nav-submenu-text-aid>ul>li:first-child>a, .nav-submenu-visual-aid>ul>li:first-child>a {
    color: skyblue;
    font-size: 14px;
}

.nav-submenu-text-aid>ul>li>a, .nav-submenu-visual-aid>ul>li>a {
    text-transform: capitalize;
    font-size: 12px;
}

